
"use client";

import * as React from "react";
import Image from "next/image";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { MapPin } from "lucide-react";

interface MapPlaceholderProps {
  latitude?: number | null;
  longitude?: number | null;
  locationName?: string | null;
}

// Basic debounce function
function debounce<F extends (...args: any[]) => void>(func: F, wait: number): (...args: Parameters<F>) => void {
  let timeoutId: ReturnType<typeof setTimeout> | null = null;
  return function(this: ThisParameterType<F>, ...args: Parameters<F>) {
    if (timeoutId !== null) {
      clearTimeout(timeoutId);
    }
    timeoutId = setTimeout(() => func.apply(this, args), wait);
  };
}


export function MapPlaceholder({ latitude, longitude, locationName }: MapPlaceholderProps) {
  const [mapUrl, setMapUrl] = React.useState<string | null>(null);
  const [isLoading, setIsLoading] = React.useState<boolean>(false);
  const [error, setError] = React.useState<string | null>(null);

  const generateMapUrl = React.useCallback(async (lat: number, lon: number) => {
     setIsLoading(true);
     setError(null);
     // Placeholder: In a real scenario, you might fetch a static map image URL
     // For now, we use picsum.photos as a stand-in, using coords slightly to vary the image.
     // A better approach would be a static map API (like Mapbox or Google Static Maps)
     // but that requires API keys and setup.
     try {
        // Simulate fetching a map image
        await new Promise(resolve => setTimeout(resolve, 500)); // Simulate network delay
        const width = 600;
        const height = 400;
        // Use coordinates to slightly change the image seed for variation
        const seed = Math.round(lat * 100 + lon * 100);
         setMapUrl(`https://picsum.photos/seed/${seed}/${width}/${height}`);
     } catch (e) {
         setError("Could not load map image.");
         console.error("Map generation error:", e);
     } finally {
        setIsLoading(false);
     }
  }, []);

  // Debounced version of generateMapUrl
   const debouncedGenerateMapUrl = React.useMemo(
       () => debounce(generateMapUrl, 1000),
       [generateMapUrl]
     );


  React.useEffect(() => {
      if (latitude != null && longitude != null) {
         debouncedGenerateMapUrl(latitude, longitude);
      } else {
         setMapUrl(null); // Clear map if no coordinates
         setIsLoading(false);
         setError(null);
      }
      // Cleanup debounce timer on unmount or dependency change
      return () => {
           if ((debouncedGenerateMapUrl as any).cancel) {
              (debouncedGenerateMapUrl as any).cancel(); // If debounce library supports cancel
           }
         };
    }, [latitude, longitude, debouncedGenerateMapUrl]);


  return (
    <Card className="shadow-sm">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <MapPin className="h-5 w-5 text-primary" />
          Location Visualizer
        </CardTitle>
      </CardHeader>
      <CardContent>
        {isLoading && <p className="text-muted-foreground text-sm">Loading map...</p>}
        {error && <p className="text-destructive text-sm">{error}</p>}
        {!isLoading && !error && mapUrl && latitude != null && longitude != null && (
          <div className="aspect-video w-full relative overflow-hidden rounded-md border">
             <Image
                 src={mapUrl}
                 alt={`Map placeholder for ${locationName || 'selected location'}`}
                 fill
                 style={{ objectFit: "cover" }}
                 data-ai-hint="map aerial view"
             />
          </div>

        )}
         {!isLoading && !error && !mapUrl && (
          <div className="aspect-video w-full flex items-center justify-center bg-muted/50 rounded-md border border-dashed">
            <p className="text-muted-foreground text-center p-4">
              {locationName
                ? `Enter coordinates or use "Use Current Location" to see map for ${locationName}`
                : "Enter a location to see a map preview."}
            </p>
          </div>
        )}
         {locationName && <p className="text-sm text-muted-foreground mt-2">Showing map for: {locationName}</p>}
         {latitude != null && longitude != null && (
           <p className="text-xs text-muted-foreground">Lat: {latitude.toFixed(4)}, Lon: {longitude.toFixed(4)}</p>
         )}
      </CardContent>
    </Card>
  );
}
