'use server';

/**
 * @fileOverview Flow to improve crop suggestions by providing reasons and actions.
 *
 * - improveCropSuggestions - A function that takes crop suggestions and provides reasons and actions to increase confidence.
 * - ImproveCropSuggestionsInput - The input type for the improveCropSuggestions function.
 * - ImproveCropSuggestionsOutput - The return type for the improveCropSuggestions function.
 */

import {ai} from '@/ai/ai-instance';
import {z} from 'genkit';

const ImproveCropSuggestionsInputSchema = z.object({
  cropSuggestions: z
    .array(z.string())
    .describe('An array of suggested crops for the given conditions.'),
  location: z.string().describe('The location for which the crop suggestions are made.'),
  soilType: z.string().describe('The soil type of the location.'),
  climateConditions: z.string().describe('The climate conditions of the location.'),
  historicalYieldData: z.string().describe('Historical yield data for the location.'),
});
export type ImproveCropSuggestionsInput = z.infer<
  typeof ImproveCropSuggestionsInputSchema
>;

const ImproveCropSuggestionsOutputSchema = z.object({
  improvedSuggestions: z.array(
    z.object({
      crop: z.string().describe('The suggested crop.'),
      reasons: z.array(z.string()).describe('Reasons for suggesting the crop.'),
      suggestedActions: z
        .array(z.string())
        .describe('Suggested actions to increase confidence in the crop.'),
    })
  ),
});
export type ImproveCropSuggestionsOutput = z.infer<
  typeof ImproveCropSuggestionsOutputSchema
>;

export async function improveCropSuggestions(
  input: ImproveCropSuggestionsInput
): Promise<ImproveCropSuggestionsOutput> {
  return improveCropSuggestionsFlow(input);
}

const prompt = ai.definePrompt({
  name: 'improveCropSuggestionsPrompt',
  input: {
    schema: z.object({
      cropSuggestions: z
        .array(z.string())
        .describe('An array of suggested crops for the given conditions.'),
      location: z.string().describe('The location for which the crop suggestions are made.'),
      soilType: z.string().describe('The soil type of the location.'),
      climateConditions: z.string().describe('The climate conditions of the location.'),
      historicalYieldData: z.string().describe('Historical yield data for the location.'),
    }),
  },
  output: {
    schema: z.object({
      improvedSuggestions: z.array(
        z.object({
          crop: z.string().describe('The suggested crop.'),
          reasons: z.array(z.string()).describe('Reasons for suggesting the crop.'),
          suggestedActions: z
            .array(z.string())
            .describe('Suggested actions to increase confidence in the crop.'),
        })
      ),
    }),
  },
  prompt: `For the following crop suggestions, provide reasons for the suggestions and suggested actions to increase confidence in the crop. Consider the location, soil type, climate conditions and historical yield data.

Crop Suggestions: {{{cropSuggestions}}}
Location: {{{location}}}
Soil Type: {{{soilType}}}
Climate Conditions: {{{climateConditions}}}
Historical Yield Data: {{{historicalYieldData}}}

Format the output as a JSON array of objects, where each object has the following keys:
- crop: The suggested crop.
- reasons: An array of reasons for suggesting the crop.
- suggestedActions: An array of suggested actions to increase confidence in the crop.

Make sure that the suggested actions are concrete and actionable.
`,
});

const improveCropSuggestionsFlow = ai.defineFlow<
  typeof ImproveCropSuggestionsInputSchema,
  typeof ImproveCropSuggestionsOutputSchema
>(
  {
    name: 'improveCropSuggestionsFlow',
    inputSchema: ImproveCropSuggestionsInputSchema,
    outputSchema: ImproveCropSuggestionsOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
